<template>
  <div class="address">
    <hx-Return :title="title"></hx-Return>
    <van-address-list
      v-model="chosenAddressId"
      :list="lists"
      default-tag-text="默认"
      @add="onAdd"
      @edit="onEdit"
      @click-item="select"
    />
  </div>
</template>

<script>
import hxReturn from "../components/hxReturn";

export default {
  data() {
    return {
      title: "收货地址",
      chosenAddressId: "1",
      lists:[

      ]

    };
  },
  methods: {
    // 直接调用vuex里面方法
    onAdd() {
      this.$router.push({name:'add'})
    },
    onEdit(item, index) {
      this.$router.push({name:'edit',query: {obj:item,index:index}})
    },
    select(e) {
      // console.log(e.id);
      this.lists.forEach(v => {
        v.isDefault = false;
      });
      this.lists[e.id - 1].isDefault = true;
    }
  },

  created() {
    this.lists = this.$store.state.address.lists;
    this.chosenAddressId = this.$store.state.address.chosenAddressId;
    //隐藏导航
    this.$store.state.vantTab = false;
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    this.$store.state.address.chosenAddressId = this.chosenAddressId
  },
  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.address {
  padding-top: 44px;
}
</style>